<template>
  <div class="child"
       v-if="is_del">
    <div>
      <div class="child_row"
           @click="showChildren = !showChildren">
        <span class="child_companyName"
              :style="{paddingLeft: level*10+'px'}">
          <v-icon :style="{ visibility: companyObj.children&&companyObj.children.length > 0 ? 'visible' : 'hidden'}"
                  class="icon"
                  :class="{'transformIcon': showChildren}">mdi-menu-down</v-icon>
          {{INT(companyObj.org_level_name)}}
        </span>
        <menu-list :list='list'
                   @handleClick='handleClick'>
          <template v-slot:showAction>
            <v-icon v-btnAuth='["view-corporate-password-policy",
                                "edit-corporate-password-policy"]'>mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </div>
      <div v-if="showChildren && companyObj.children.length > 0">
        <child-company v-for="item in companyObj.children"
                       :key='item.uuid'
                       :level='level+1'
                       @delData='delData'
                       :companyObj='item' />
      </div>
    </div>
  </div>
</template>

<script>
import { get_company_del } from '@/service/api/company_set'
export default {
  name: 'childCompany',
  components: {},
  props: {
    companyObj: {
      type: Object
    },
    level: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      is_del: true,
      showChildren: false,

    };
  },
  created () { },
  mounted () { },
  methods: {
    handleClick ({ item, index }) {
      switch (index) {
        case 0:
          this.$store.commit('set_iconstatus', true)  //行内进图标显示
          this.$store.commit('set_subPage_name', this.companyObj.org_level_name)
          this.$store.commit('set_uuid', this.companyObj.uuid)
          this.$store.commit('setOrgLevelCode', this.companyObj.org_level_code)
          this.$router.push({
            name: 'detailPasswordIntensity',
            params: {
              name: this.companyObj.org_level_name,
              uuid: this.companyObj.uuid
            }
          })
          break;
        case 1:
          this.$store.commit('set_iconstatus', false)  //行内进图标显示
          this.$store.commit('set_subPage_name', this.companyObj.org_level_name)
          this.$store.commit('set_uuid', this.companyObj.uuid)
          this.$store.commit('setOrgLevelCode', this.companyObj.org_level_code)
          this.$router.push({
            name: 'detailPasswordIntensity',
            params: {
              name: this.companyObj.org_level_name,
              uuid: this.companyObj.uuid
            }
          })
          break
      }
    },
    delData (uuid) {
      this.$emit('delData', uuid)
    }
  },
  computed: {
    list () {
      return [
        {
          name: this.$t('label_View'),
          show: this.hasBtn("view-corporate-password-policy")
        },
        {
          name: this.$t('label_Edit'),
          show: this.hasBtn("edit-corporate-password-policy")
        }

      ]
    }
  },
  watch: {},
};
</script>

<style scoped>
.child_row {
  width: 100%;
  height: 55px;
  border-top: 1px solid #c5c5c5;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
}
.child_companyName {
  font-size: 16px;
  color: #000000;
  display: flex;
  align-items: center;
  /* font-weight: 600; */
  /* color: #ff9100; */
  letter-spacing: 3px;
}
.icon {
  transform: rotate(-90deg);
}

.transformIcon {
  transform: rotate(0deg);
}
.child_row:hover {
  background-color: #e4dfdf;
}
</style>
